/* ==========================================================================
   WooCommerce Quantity Inputs styles - begin
   ========================================================================== */

.woocommerce-page .mkdf-content,
div.woocommerce {
    .mkdf-quantity-buttons {
        $size: 67px;

        background: $additional-background-color;
        display: inline-block;
        position: relative;
        vertical-align: middle;
        border-radius: $default-border-radius;

        .mkdf-quantity-input,
        .mkdf-quantity-minus,
        .mkdf-quantity-plus {
            background-color: transparent;
            border: none;
            box-sizing: border-box;
            color: $default-heading-color;
            display: inline-block;
            vertical-align: middle;
        }

        .mkdf-quantity-input {
            font-family: inherit;
            height: $size;
            line-height: $size;
            margin: 0 #{$size / 2} 0 0;
            outline: 0;
            padding: 0;
            position: relative;
            text-align: center;
            width: $size;

            @include mkdfH5();
        }

        .mkdf-quantity-minus,
        .mkdf-quantity-plus {
            border-left: 0;
            cursor: pointer;
            font-weight: bold;
            height: #{$size / 2};
            line-height: #{$size / 2};
            position: absolute;
            right: 0;
            text-align: left;
            width: #{$size / 2};

            @include mkdfTransition(color .15s ease-in-out);

            &:hover {
                color: $first-main-color;
            }
        }

        .mkdf-quantity-plus {
            border-bottom: 0;
            top: 0;
        }

        .mkdf-quantity-minus {
            bottom: 0;
        }
    }
}

.woocommerce-page .mkdf-content,
div.woocommerce {
    input[type="text"],
    input[type="email"],
    input[type="tel"],
    input[type="password"],
    textarea {
        @include mkdfInputStyle(small);
    }

    .coupon {
        input[type="text"] {
            width: 200px;
        }
    }
}

/* ==========================================================================
   WooCommerce Quantity Inputs styles - end
   ========================================================================== */