/* ==========================================================================
   Marks (Sale, Out of Stock) mixin styles - begin
   ========================================================================== */

@mixin mkdfWooCommerceMarksGeneralStyle() {
    position: absolute;
    display: block;
    top: 20px;
    width: auto;
    height: 20px;
    margin: 0;
    padding: 1px 20px;
    color: #ffffff;
    font-size: 15px;
    line-height: 20px;
    letter-spacing: 1px;
    text-align: center;
    z-index: 100;
    box-sizing: border-box;
    border-radius: 3px;

    @include mkdfTransform(translateZ(0));
}

@mixin mkdfWooCommerceSaleStyle() {
    background-color: $second-main-color;
    left: 20px;
}

@mixin mkdfWooCommerceOutOfStockStyle() {
    background-color: #000000;
    right: 20px;
}

@mixin mkdfWooCommerceNewProductStyle() {
    background-color: #000000;
    right: 20px;
}

/* ==========================================================================
   Marks (Sale, Out of Stock) mixin styles - end
   ========================================================================== */

/* ==========================================================================
   Price mixin styles - begin
   ========================================================================== */

@mixin mkdfWooCommercePriceStyle($source:list) {
    @if ($source=='list') {
        font-size: 24px;
    } @else if ($source=='single') {
        font-size: 34px;
    }

    color: $default-heading-color;
    display: block;
    line-height: 1;
    margin: 8px 0 0 0;
    position: relative;

    * {
        font-size: inherit;
        line-height: inherit;
        text-decoration: none;
    }

    del {
        margin: 0 8px 0 0;
        position: relative;

        &:before {
            background: $second-main-color;
            content: '';
            height: 1px;
            left: -5px;
            position: absolute;
            top: 50%;
            width: calc(100% + 10px);
        }
    }

    ins {
        //text-decoration: none;
    }
}

/* ==========================================================================
   Price mixin styles - end
   ========================================================================== */

/* ==========================================================================
   Star Ratings mixin styles - begin
   ========================================================================== */

%star-ratings {
    font-family: 'IonIcons';
    font-size: 20px;
    line-height: 1;
    letter-spacing: 0.08em;
    color: $default-text-color;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

@mixin mkdfWooCommerceRatingsStyle() {
    position: relative;
    display: inline-block;
    vertical-align: middle;

    @extend %star-ratings;

    overflow: hidden;

    &:before {
        content: "\f3ae\f3ae\f3ae\f3ae\f3ae";
    }

    span {
        position: absolute;
        top: 0;
        left: 0;
        overflow: hidden;

        &:before {
            color: $second-main-color;
            content: "\f2fc\f2fc\f2fc\f2fc\f2fc";
        }
    }
}

@mixin mkdfWooCommerceSingleProductRatingsStyle() {
    &:before {
        content: "\f3ae";
        position: relative;
        display: inline-block;
        vertical-align: middle;

        @extend %star-ratings;

        overflow: hidden;
        z-index: -1;
    }

    &.active:after {
        content: "\f2fc\f2fc\f2fc\f2fc\f2fc";
        position: absolute;
        right: 0;
        bottom: 5px;
        display: block;
        color: $second-main-color;

        @extend %star-ratings;
    }
}

/* ==========================================================================
   Star Ratings mixin styles - end
   ========================================================================== */

// product list buttons

@mixin mkdfProductListButton($type:link) {
    $size: 48px;

    background: #000000;
    border-radius: $default-border-radius;
    border: none;
    bottom: 10px;
    color: #ffffff;
    font-size: 0;
    font-weight: normal;
    height: $size;
    left: 50%;
    line-height: $size;
    margin: 0;
    padding: 0;
    position: absolute;
    width: $size;
    z-index: 1;

    @include mkdfTransition(background-color .2s ease-in-out);
    @include mkdfTransform(translateX(-50%));

    &:hover {
        background: $first-main-color;
    }

    &:before {
        font-family: 'IonIcons';
        font-size: 25px;
        height: $size;
        left: 0;
        line-height: $size;
        position: absolute;
        text-align: center;
        top: 0;
        width: $size;

        @if ($type=='add') {
            content: '\f110';
        } @else if ($type=='added') {
            content: '\f424';
        } @else if ($type=='link') {
            content: '\f39c';
        }
    }
}

// title addon
@mixin mkdfWooTitleAddon() {
    padding-left: 220px;
    position: relative;

    &:before {
        content: '01.';
        position: absolute;
        top: 0;
        left: 0;
    }

    &:after {
        background: #000000;
        content: '';
        display: block;
        height: 2px;
        position: absolute;
        top: 14px;
        left: 28px;
        width: 180px;
    }
}

// fake tr border
@mixin mkdfWooFakeBorder($position) {
    overflow: hidden;

    > *:first-child {
        position: relative;
        //background: $additional-background-color;

        &:before {
            content: '';
            display: block;
            width: 1000vh;
            height: 0;
            position: absolute;
            left: 0;
            border-bottom: 1px dotted #000000;

            @if ($position==top) {
                top: 0;
            } @else {
                bottom: 0;
            }
        }
    }
}