/* ==========================================================================
   Product List shortcode styles - begin
   ========================================================================== */

.mkdf-pl-holder {
    @include mkdfRelativeHolderLayout();

    //.mkdf-pli {
    //    .mkdf-pli-add-to-cart {
    //        margin: 25px 0 0;
    //    }
    //}

    .mkdf-pli {
        padding: 0;
        z-index: 100;

        .mkdf-pli-title {
            margin: 0;
        }

        .mkdf-pli-rating-holder {
            @include mkdfRelativeHolderLayout();

            line-height: 1;
            margin: 8px 0;
        }

        .mkdf-pli-rating {
            @include mkdfWooCommerceRatingsStyle();
        }

        .mkdf-pli-price {
            @include mkdfWooCommercePriceStyle();
        }
    }

    .mkdf-pli-inner {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        overflow: hidden;

        &:hover {
            .mkdf-pli-text {
                opacity: 1;
            }
        }

        .touch & {
            cursor: pointer;
        }

        .mkdf-pli-image {
            @include mkdfRelativeHolderLayout();

            img {
                display: block;
                width: 100%;
            }

            .mkdf-pli-onsale,
            .mkdf-pli-out-of-stock,
            .mkdf-pli-new-product {
                @include mkdfWooCommerceMarksGeneralStyle();
            }

            .mkdf-pli-onsale {
                @include mkdfWooCommerceSaleStyle();
            }

            .mkdf-pli-out-of-stock {
                @include mkdfWooCommerceOutOfStockStyle();
            }

            .mkdf-pli-new-product {
                @include mkdfWooCommerceNewProductStyle();
            }
        }

        .mkdf-pli-text {
            background-color: #ffffff;
            box-sizing: border-box;
            display: block;
            height: calc(100% - 30px);
            left: 15px;
            opacity: 0;
            padding: 10px;
            position: absolute;
            top: 15px;
            width: calc(100% - 30px);

            @include mkdfTransition(opacity .2s ease-out);
        }

        .mkdf-pli-text-outer {
            @include mkdfTableLayout();

            text-align: center;
        }

        .mkdf-pli-text-inner {
            @include mkdfTableCellLayout();

