/* ==========================================================================
   WooCommerce Widgets styles - begin
   ========================================================================== */

.widget.woocommerce {
    $thumb: 68px;
    $inner-padding: 25px; // distance between image and content
    $widget-list-item-padding: 11px; // same as sidebar vars

    clear: both;

    li {
        position: relative;

        // clearfix

        &:before,
        &:after {
            content: " "; /* 1 */
            display: table; /* 2 */
        }

        &:after {
            clear: both;
        }

        & {
            *zoom: 1;
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // title

    .product-title {
        @include mkdfH5();
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // star rating

    li:not(.wc-layered-nav-rating) {
        .star-rating {
            margin-bottom: 6px;
            margin-top: 0;

            span {
                max-width: 100px;
            }
        }
    }

    li.wc-layered-nav-rating {
        a {
            text-decoration: none;
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // price

    .amount {
        color: inherit;

        // currency symbol
        span {
            color: inherit;
        }
    }

    del,
    ins {
        display: inline-block;
        position: relative;
        text-decoration: none;

        // currency symbol
        span {
            color: inherit;
        }
    }

    del {
        &:before {
            background: $second-main-color;
            content: '';
            height: 1px;
            position: absolute;
            width: calc(100% + 10px);
            top: 50%;
            left: -5px;
        }
    }

    ins {
        clear: none !important;
        margin: 0 0 0 10px !important;
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // layered nav

    &.widget_layered_nav {
        .chosen {
            a {
                color: $first-main-color;
            }
        }

        .woocommerce-widget-layered-nav-dropdown {
            button {
                @include mkdfButtonSize(shop);
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // price filter

    &.widget_price_filter {
        form {
            margin-top: 20px;
        }

        .price_slider_wrapper {
            .ui-widget-content {
                background-color: #f1f1f1;
                height: 5px;
                position: relative;

                .ui-slider-handle {
                    background-color: $second-main-color;
                    border-radius: 3px;
                    cursor: pointer;
                    height: 19px;
                    margin: 0 0 0 -1px;
                    outline: medium none;
                    position: absolute;
                    top: -7px;
                    width: 6px;
                    z-index: 2;

                    &:last-child {
                        transform: translateX(-50%);
                    }
                }

                .ui-slider-range {
                    background-color: $second-main-color;
                    display: block;
                    height: 5px;
                    position: absolute;
                    top: 0;
                    z-index: 1;
                }
            }
        }

        .price_slider_amount {
            margin: 32px 0 0;

            .button {
                float: left;

                @include mkdfButtonSize(shop);
            }

            .price_label {
                float: right;
                line-height: 41px; // to match button height
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // widgets with images

    &.widget_shopping_cart,
    &.widget_products,
    &.widget_recently_viewed_products,
    &.widget_recent_reviews,
    &.widget_top_rated_products {
        ul {
            li {
                min-height: $thumb;
                position: relative;

                a {
                    img {
                        height: $thumb;
                        left: 0;
                        position: absolute;
                        top: $widget-list-item-padding;
                        width: $thumb;
                        object-fit: cover;
                    }

                    span {
                        box-sizing: border-box;
                        display: block;
                        padding: 0 0 0 #{$thumb + $inner-padding};
                        position: relative;
                    }
                }

                > *:not(a) {
                    clear: both;
                    float: left;
                    margin: 0 0 0 #{$thumb + $inner-padding};
                }
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // widgets with images and plain text next to image

    &.widget_shopping_cart {
        a {
            display: block;
            padding: 0 0 0 #{$thumb + $inner-padding};

            @include mkdfH5();
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // recent reviews

    //&.widget_recent_reviews {
    //    .reviewer {
    //    }
    //}

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // shopping cart

    &.widget_shopping_cart {
        .remove {
            font-size: 0;
            font-weight: normal;
            line-height: 1;
            position: absolute;
            right: 0;
            text-decoration: none;
            top: 5px;

            &:after {
                content: "\4d";
                display: block;
                font-family: 'ElegantIcons';
                font-size: 15px;
                line-height: 15px;
                position: relative;
            }
        }

        .variation {
            @include mkdfRelativeHolderLayout();

            > * {
                display: inline-block;
                margin: 0;
                vertical-align: middle;

                p {
                    margin: 0;
                }
            }
        }

        .total {
            margin: 17px 0 13px;
        }

        .buttons {
            .button {
                @include mkdfButtonSize(shop);

                clear: both;
                float: left;

                &:last-child {
                    margin-top: 15px;
                }
            }
        }
    }
}

/* ==========================================================================
   WooCommerce Widgets styles - end
   ========================================================================== */