/* ==========================================================================
   WooCommerce Buttons styles - begin
   ========================================================================== */

.woocommerce-page .mkdf-content,
div.woocommerce {
    a.button,
    a.added_to_cart,
    input[type="submit"],
    button[type="submit"]:not(.mkdf-search-submit):not(.single_add_to_cart_button),
    .wc-forward:not(.added_to_cart):not(.checkout-button),
    .single_add_to_cart_button {
        @include mkdfButtonDefaultStyle();
        @include mkdfButtonSize(huge);
        @include mkdfButtonSolidColor();
        cursor: pointer;
        z-index: 3;

        &:hover {
            @include mkdfButtonSolidHoverColor();
        }
    }

    a.button.added {
        display: none;
    }
}

ul.products li.product,
.mkdf-pli {
    a {
        &.add_to_cart_button,
        &.product_type_external {
            @include mkdfProductListButton(add);
        }

        &.added_to_cart {
            @include mkdfProductListButton(added);
        }

        &.product_type_variable,
        &.out_of_stock {
            @include mkdfProductListButton(link);
        }
    }
}

ul.products li.product {
    &.outofstock {
        a.button {
            @include mkdfProductListButton(link);
        }
    }
}

/* ==========================================================================
   WooCommerce Buttons styles - end
   ========================================================================== */