/* ==========================================================================
   WooCommerce Single Product Page styles - begin
   ========================================================================== */

.mkdf-woo-single-page {
    &.mkdf-woo-single-thumb-on-left-side {
        .mkdf-single-product-content {
            .images {
                @media only screen and (min-width: $ipad-portrait-plus-pixel) {

                    figure {
                        width: 100%;
                        margin: 0;
                        overflow: hidden;
                    }

                    $thumb_size: 192;
                    $space_between_items: 26;

                    .woocommerce-product-gallery__image {
                        &:first-child {
                            $first_thumb_offset: $thumb_size + $space_between_items;

                            left: #{$first_thumb_offset}px;
                            width: calc(100% - #{$first_thumb_offset}px);
                        }
                    }

                    &.woocommerce-product-gallery--with-images {
                        .woocommerce-product-gallery__image {
                            &:not(:first-child) {
                                position: absolute;
                                top: 0;
                                left: 0;
                                width: #{$thumb_size}px;
                                padding: 0 0 #{$space_between_items}px;
                                box-sizing: border-box;

                                a {
                                    height: 100%;

                                    img {
                                        height: 100%;
                                    }
                                }
                            }
                        }
                    }

                    $thumb_3_columns_label: ('columns-2', 'columns-3', 'columns-4');
                    @for $i from 0 to length($thumb_3_columns_label) {
                        &.woocommerce-product-gallery--#{nth($thumb_3_columns_label, $i+1)} {
                            $column_value: $i+2;
                            $column_size: 100 / $column_value;
                            $column_width: calc(#{100% / $column_value} + #{$space_between_items / $column_value}px);

                            .woocommerce-product-gallery__image {

                                &:not(:first-child) {
                                    height: $column_width;
                                }

                                $thumbnail_numbers: (2, 3, 4, 5, 6, 7, 8, 9);
                                @for $i from 0 to length($thumbnail_numbers) {
                                    $thumbnail_number_value: nth($thumbnail_numbers, $i+1);

                                    &:nth-child(#{$thumbnail_number_value}#{n}) {
                                        $first_param: ($column_size * $i)#{'%'};
                                        $second_param: ($space_between_items / $column_value * $i)#{px};
                                        $topPos: calc(#{$first_param} + #{$second_param});

                                        top: $topPos;
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
    }

    .product {
        @include mkdfRelativeHolderLayout();

        float: none;
        margin: 0 0 110px;
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // left side stuff

    .mkdf-single-product-content {
        @include mkdfRelativeHolderLayout();

        clear: both;
        margin: 0 0 20px;

        .images {
            box-sizing: border-box;
            display: inline-block;
            float: left;
            margin: 0;
            padding: 0 26px 0 0;
            position: relative;
            vertical-align: middle;
            width: 66%;

            figure {
                display: inline-block;
                margin: 0 -10px;
                position: relative;
                vertical-align: top;

                a {
                    @include mkdfRelativeHolderLayout();

                    img {
                        display: block;
                        position: relative;
                        width: 100%;
                    }
                }
            }

            .woocommerce-product-gallery__trigger {
                height: 30px;
                line-height: 30px;
                position: absolute;
                right: 39px;
                text-align: center;
                top: 10px;
                width: 30px;
                z-index: 10;
            }

            .woocommerce-product-gallery__image {
                box-sizing: border-box;
                display: inline-block;
                margin: 0;
                padding: 0 10px;
                position: relative;
                vertical-align: top;

                &:first-child {
                    left: 10px;
                    padding: 0;
                    width: calc(100% - 20px);
                }

                .zoomImg {
                    cursor: crosshair;
                }
            }

            &.woocommerce-product-gallery--with-images {
                .woocommerce-product-gallery__image {
                    &:not(:first-child) {
                        float: left;
                        padding-top: 20px;
                    }
                }
            }

            $thumb_3_columns_label: ('columns-2', 'columns-3', 'columns-4');
            @for $i from 0 to length($thumb_3_columns_label) {
                &.woocommerce-product-gallery--#{nth($thumb_3_columns_label, $i+1)} {
                    $column_width: 100%/($i+2);

                    .woocommerce-product-gallery__image {
                        &:not(:first-child) {
                            width: $column_width;
                        }
                    }
                }
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // right side stuff

    .mkdf-single-product-summary {
        box-sizing: border-box;
        display: inline-block;
        float: left;
        padding: 0 0 0 26px;
        position: relative;
        vertical-align: middle;
        width: 34%;

        .mkdf-single-product-title {
            display: block;
            margin: 0;
        }

        .woocommerce-product-rating {
            @include mkdfRelativeHolderLayout();

            line-height: 1;
            margin: 7px 0 14px;
        }

        .price {
            @include mkdfHeadingStyle(h2);
            @include mkdfWooCommercePriceStyle(single);

            margin: 0 0 48px 0;
        }

        .woocommerce-product-details__short-description {
            @include mkdfRelativeHolderLayout();

            margin: 0 0 70px 0;

            p {
                margin: 0;
            }
        }

        p.stock.out-of-stock,
        p.stock.in-stock {
            color: $default-heading-color;
            margin: 0 0 70px 0;

            @include mkdfH4();
        }

        form.cart {
            @include mkdfRelativeHolderLayout();

            margin: 0 0 70px 0;
        }

        .mkdf-quantity-buttons {
            margin: 0 43px 0 0;
        }

        .product_meta {
            @include mkdfH6();

            > span {
                display: block;
                margin-bottom: 15px;
            }

            .sku, a {
                @include mkdfH5();
            }

            .sku, a:first-child {
                margin-left: 9px;
            }
        }

        .mkdf-woo-social-share-holder {
            @include mkdfRelativeHolderLayout();

            margin: 0;

            > * {
                display: table-cell;
                vertical-align: top;
            }

            > span {
                padding: 0 9px 0 0;
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // tabs

    .woocommerce-tabs {
        @include mkdfRelativeHolderLayout();

        margin: 30px 0 0;

        ul.tabs {
            @include mkdfRelativeHolderLayout();

            border-bottom: 1px dotted #000000;
            clear: both;
            list-style: none;
            margin: 0;
            padding: 0;
            text-align: center;

            > li {
                display: inline-block;
                position: relative;
                vertical-align: middle;

                a {
                    display: inline-block;
                    padding: 37px 76px;
                    position: relative;
                    vertical-align: top;
                    color: $first-main-color;

                    @include mkdfH2();

                    font-size: 30px;

                    &:hover {
                        color: $default-heading-color;
                    }
                }

                &.active {
                    a {
                        color: $default-heading-color;
                    }
                }
            }
        }

        .woocommerce-Tabs-panel {
            padding: 30px 14.3% 0;
        }

        .entry-content {
            > h2 {
                display: none;
            }

            > p {
                margin: 20px 0 0 0;
            }
        }

        table {
            margin: 23px 0 0 0;

            tr, th {
                border: 0;
            }

            td, th {
                padding: 3px 0;
                text-align: left;
                vertical-align: middle;

                p {
                    margin: 0;
                }
            }

            th {
                color: $default-heading-color;
                padding-right: 10px;
                width: 1%;

                @include mkdfH6();
            }

            td {
                width: 99%;

                @include mkdfH5();
            }
        }

        // reviews - start
        #reviews {
            @include mkdfRelativeHolderLayout();

            margin: 16px 0 0 0;

            h2 {
                //@include mkdfH3();

                margin: 8px 0 45px;
            }

            ol.commentlist {
                list-style: none;

                li {
                    margin: 0 0 49px;

                    &:last-child {
                        margin: 0;
                    }
                }

                .comment_container {
                    @include mkdfRelativeHolderLayout();

                    position: initial;

                    img {
                        float: left;
                        width: 70px;
                        position: relative;
                        top: 6px;
                    }
                }

                .comment-text {
                    padding: 0 0 20px 90px;
                    position: relative;

                    p {
                        @include mkdfH6();

                        margin: 0;
                    }

                    .description {
                        margin: 11px 0 28px 0;
                    }
                }

                .woocommerce-review__author {
                    @include mkdfH5();
                }

                .woocommerce-review__dash {
                    display: none;
                }

                .woocommerce-review__published-date {
                    @include mkdfH6();

                    position: absolute;
                    bottom: 0;
                    left: 90px;
                }
            }

            .comment-respond {
                margin-top: 45px;

                .comment-reply-title {
                    @include mkdfH3();

                    display: inline-block;
                    margin: 0 0 7px;
                }

                p {
                    margin: 0;
                }

                label {
                    @include mkdfH5();

                    display: inline-block;
                }

                .comment-form-rating {
                    margin-bottom: 6px;

                    .stars {
                        margin-top: -12px;
                        overflow: hidden;

                        a {
                            color: transparent;
                            display: inline-block;
                            font-size: 0;
                            margin: 0;
                            position: relative;

                            @include mkdfWooCommerceSingleProductRatingsStyle();
                        }
                    }
                }

                .comment-form-comment {
                    label {
                        margin-bottom: 8px;
                    }
                }
            }
        }

        // reviews - end
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // related

    .related.products,
    .upsells.products {
        margin: 118px 0 0 0;

        > h2 {
            display: block;
            margin: 36px 0 78px;

            @include mkdfH5();
            @include mkdfWooTitleAddon();
        }

        .product {
            .mkdf-pl-text-wrapper {
                margin-bottom: 0;
            }
        }
    }
}

/* ==========================================================================
   WooCommerce Single Product Page styles - end
   ========================================================================== */