/* ==========================================================================
   WooCommerce Dropdown Cart styles - begin
   ========================================================================== */

.mkdf-shopping-cart-holder {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    height: 100%;

    .touch & {
        cursor: pointer;
    }

    .mkdf-shopping-cart-inner {
        position: relative;
        height: 100%;
    }

    .mkdf-header-cart {
        position: relative;
        display: flex;
        height: 100%;
        align-items: center;
        line-height: 1;

        .mkdf-cart-icon {
            position: relative;
            display: inline-block;
            vertical-align: top;
        }

        .mkdf-cart-number {
            @include mkdfH6();
        }

        &.mkdf-header-cart-icon-pack {
            .mkdf-cart-icon {
                font-size: 25px;
                line-height: 1em;

                * {
                    display: block;
                    line-height: inherit;

                    &:before {
                        display: block;
                        line-height: inherit;
                    }
                }
            }
        }

        &.mkdf-header-cart-svg-path {
            .mkdf-cart-icon {
                > * {
                    vertical-align: middle;
                }

                > *,
                path {
                    fill: currentColor;
                    stroke: currentColor;
                }
            }
        }
    }

    &:hover {
        .mkdf-shopping-cart-dropdown {
            //top: 100%;
            top: calc(100% + 2px);
            opacity: 1;
            visibility: visible;
        }
    }
}

