.mkdf-shopping-cart-dropdown {
    position: absolute;
    top: calc(100% + 50px);
    left: auto;
    right: 0;
    width: 331px;
    background-color: #000000;
    text-align: left;
    z-index: 1000;
    opacity: 0;
    visibility: hidden;
    box-sizing: border-box;
    color: #ffffff;

    @include mkdfTransition(all 0.35s cubic-bezier(.53, .01, .18, 1));

    ul {
        margin: 30px;
        padding: 0;

        li {
            @include mkdfTableLayout();

            padding: 0 0 25px 0;
            box-sizing: border-box;

            &.mkdf-empty-cart {
                color: inherit;
            }

            &:last-child {
                padding-bottom: 0;
            }
        }
    }

    .mkdf-item-image-holder {
        position: relative;
        display: table-cell;
        height: 100%;
        width: 76px;
        vertical-align: top;

        img {
            display: block;
        }
    }

    .mkdf-item-info-holder {
        @include mkdfTableCellLayout();

        vertical-align: top;
        padding-left: 25px;
        color: inherit;

        .mkdf-product-title {
            display: block;
            margin: 0;
            padding: 0 20px 0 0;
            box-sizing: border-box;
            color: inherit;
        }

        .remove {
            position: absolute;
            top: -1px;
            right: 0;
            font-size: 20px;
            line-height: 1;
            color: inherit;

            span {
                display: block;
                line-height: inherit;

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }
        }

        .mkdf-quantity {
            display: block;
            margin: 3px 0 0;

            @include mkdfH6();
        }

        .amount {
            display: block;
            margin: 3px 0 0;

            @include mkdfH3();
        }
    }

    .mkdf-cart-bottom {
        padding: 26px 0 0;
        position: relative;

        .mkdf-cart,
        .mkdf-checkout {
            @include mkdfButtonDefaultStyle();
            @include mkdfButtonSize(shop);

            background-color: #ffffff;
            padding-left: 0 !important;
            padding-right: 0 !important;
            text-align: center;
            width: calc(50% - 15px);

            &:hover {
                color: #fff;
                background-color: #a8a8a8;
            }
        }

        .mkdf-cart {
            float: left;
        }

        .mkdf-checkout {
            float: right;
        }

        &:before {
            border-top: 1px dotted #ffffff;
            box-sizing: border-box;
            content: '';
            display: block;
            height: 1px;
            left: 0;
            position: absolute;
            top: 0;
            width: 50%;
        }
    }
}

.mkdf-vertical-menu-area {
    .mkdf-shopping-cart-dropdown {
        display: none !important;
    }
}

/* ==========================================================================
   WooCommerce Dropdown Cart styles - end
   ========================================================================== */